<template>
  <div class="container" id="cards">
    <!-- Cards Start -->
    <div class="row" v-bind:class="{ opacity: completed || canceled }">
      <p class="my-3">Тековни</p>
      <Card v-for="card in cardsOngoing" :key="card.id" :card="card" />
    </div>
    <div class="row" v-bind:class="{ opacity: completed || canceled }">
      <p class="my-3">Претходни</p>
      <Card v-for="card in cardsPast" :key="card.id" :card="card" />
    </div>
    <!-- Cards End -->
    <!-- Delete Modal Start -->
    <div class="row" v-if="canceled">
      <div class="col-lg-4 mx-auto border border-dark bg-white" id="modal">
        <div class="row" v-if="!canceledConfirmation">
          <div
            class="col-lg-1 ml-auto mt-2 mb-5"
            v-on:click="changeReservations(false, false)"
            style="cursor: pointer"
          >
            X
          </div>
          <div class="col-lg-12 text-center">
            <h1 class="mt-5">Успешнo Избришана Резервација!</h1>
            <h1><i class="fas fa-check text-success"></i></h1>
          </div>
        </div>
        <div class="row" v-if="canceledConfirmation">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5">
              Дали навистина сакате да ја откажете резервацијата?
            </h1>
          </div>
          <div class="col-lg-4 mx-auto">
            <button
              class="btn btn-dark btn-block rounded-0"
              v-on:click="deleteReservation()"
            >
              Да
            </button>
          </div>
          <div class="col-lg-4 mx-auto">
            <button
              v-on:click="changeReservations(false, false)"
              class="btn btn-dark btn-block rounded-0"
            >
              Не
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Delete Modal End -->
    <!-- Success Modal Start -->
    <div class="row" v-if="completed">
      <div class="col-lg-4 mx-auto border border-dark bg-white" id="modal">
        <div class="row" v-if="completedConfirmation">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5">
              Дали навистина сакате да ја направите резервацијата?
            </h1>
          </div>
          <div class="col-lg-4 mx-auto">
            <button
              class="btn btn-dark btn-block rounded-0"
              v-on:click="addReservation()"
            >
              Да
            </button>
          </div>
          <div class="col-lg-4 mx-auto">
            <button
              v-on:click="changeReservations(false, false)"
              class="btn btn-dark btn-block rounded-0"
            >
              Не
            </button>
          </div>
        </div>
        <div class="row" v-if="!completedConfirmation">
          <div
            class="col-lg-1 ml-auto mt-2 mb-5"
            v-on:click="changeReservations(false, false)"
            style="cursor: pointer"
          >
            X
          </div>
          <div class="col-lg-12 text-center">
            <h1 class="mt-5">Успешна Резервација!</h1>
            <h1><i class="fas fa-check text-success"></i></h1>
          </div>
        </div>
      </div>
    </div>
    <!-- Success Modal End -->
  </div>
</template>

<script>
import Card from "../components/Card.vue";
export default {
  name: "Reservations",
  components: {
    Card,
  },
  data() {
    return {
      cardsOngoing: [],
      cardsPast: [],
      completedConfirmation: true,
      canceledConfirmation: true,
      canceled: false,
      completed: false,
    };
  },
  methods: {
    changeReservations(reserve, cancel) {
      this.completed = reserve;
      this.canceled = cancel;
      this.$store.commit("changeReservations", {
        reserve: reserve,
        cancel: cancel,
      });
    },
    addReservation() {
      this.changeReservations(false, false);
      let person = this.$store.getters.selectedPerson;
      this.$store.commit("addReservation", person);
      this.$store.commit("addSelectedPerson", {});
      this.updateAds();
    },
    deleteReservation() {
      this.changeReservations(false, false);
      let person = this.$store.getters.selectedPerson;
      this.$store.commit("deleteReservation", person);
      this.$store.commit("addSelectedPerson", {});
      this.updateAds();
    },
    updateAds() {
      this.cardsOngoing = this.$store.getters.ads;
      this.cardsOngoing = this.cardsOngoing.filter((ad) => ad.ongoing == true);
      this.cardsPast = this.$store.getters.ads.filter((ad) => ad.past == true);
    },
  },
  mounted() {
    this.updateAds();
    this.canceled = this.$store.getters.cancel;
    this.completed = this.$store.getters.reserve;
  },
};
</script>

<style scoped>
.opacity {
  opacity: 0.5;
}

#modal {
  height: 50vh;
  width: 50vw;
  position: absolute;
  left: 35vw;
  top: 25vh;
}
</style>