<template>
  <div class="container">
    <p class="my-3">Филтер</p>
    <div class="row">
      <div class="col-lg-5 mx-auto">
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">Почетна дестинација</span>
          </div>
          <input
            type="text"
            class="form-control"
            v-model="start"
            v-on:keyup="filter"
          />
        </div>
      </div>

      <div class="col-lg-5 mx-auto">
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">Крајна дестинација</span>
          </div>
          <input
            type="text"
            class="form-control"
            v-model="end"
            v-on:keyup="filter"
          />
        </div>
      </div>
    </div>
    <div class="row">
      <Card v-for="ad in ads" :key="ad.id" :card="ad" :is-ad="true" />
    </div>
  </div>
</template>

<script>
import Card from "../components/Card.vue";
export default {
  name: "Advertisments",
  components: {
    Card,
  },
  data() {
    return {
      ads: [],
      start: "",
      end: "",
    };
  },
  methods: {
    filter() {
      this.ads = this.$store.getters.ads;
      console.log(this.start, this.end);
      if (this.start != "") {
        this.ads = this.ads.filter((ad) =>
          ad.startDestination.toLowerCase().includes(this.start.toLowerCase())
        );
      }
      if (this.end != "") {
        this.ads = this.ads.filter((ad) =>
          ad.endDestination.toLowerCase().includes(this.end.toLowerCase())
        );
      }
    },
  },
  mounted() {
    this.ads = this.$store.getters.ads;
  },
};
</script>

