<template>
  <div class="container">
    <h1 class="my-3">
      <strong>Вашата оценка од корисниците е:</strong>
      {{ this.averageRating }}.0
      <i class="fas fa-star text-warning"></i>
      <i class="fas fa-star text-warning"></i>
      <i class="fas fa-star text-warning"></i>
      <i class="fas fa-star text-warning"></i>
      <i class="fas fa-star text-warning"></i>
    </h1>
    <div class="row" id="cards">
      <RatingCard v-for="rating in ratings" :key="rating.id" :rating="rating" />
    </div>
  </div>
</template>

<script>
import RatingCard from "../components/RatingCard.vue";
export default {
  name: "Ratings",
  components: { RatingCard },
  data() {
    return {
      ratings: [],
      averageRating: 0.0,
    };
  },
  mounted() {
    this.ratings = this.$store.getters.ratings;
    let rates = this.ratings.map((rating) => rating.rating);
    this.averageRating =
      rates.reduce(function (sum, rating) {
        return sum + rating;
      }, 0) / rates.length;
  },
};
</script>

