<template>
  <!-- Card Start -->
  <div class="container" id="app">
    <div class="row border border-dark m-5 h-100 p-3">
      <div class="col-lg-12 mt-3">
        <div class="row">
          <div class="col-lg-3 my-auto">
            <img src="../assets/img/profile.png" class="img-fluid" />
          </div>
          <div class="col-lg-6 my-auto text-center">
            <strong>Оцена: </strong>{{ person.rating }}.0
          </div>
          <div class="col-lg-3 my-auto">
            <h4><strong>Цена: </strong><br />{{ person.price }} MKD</h4>
          </div>
        </div>
      </div>
      <div class="col-lg-12 mt-3">
        <p><strong>Име: </strong>{{ person.name }}</p>
      </div>
      <div class="col-lg-12 mt-3">
        <p><strong>Презиме: </strong>{{ person.surname }}</p>
      </div>
      <div class="col-lg-12 mt-3">
        <div class="row">
          <div class="col-lg-4">
            <p>
              <strong>Почетна дестинација: </strong
              >{{ person.startDestination }}
            </p>
          </div>
          <div class="col-lg-4">
            <p>
              <strong>Крајна дестинација: </strong>{{ person.endDestination }}
            </p>
          </div>
        </div>
      </div>
      <div class="col-lg-12 mt-3">
        <p><strong>Број на слободни места: </strong>{{ person.freeSeats }}</p>
      </div>
      <div class="col-lg-12 mt-3">
        <p><strong>Возило: </strong>{{ person.vehicle }}</p>
      </div>
      <div class="col-lg-12 mt-3" v-if="person.covidSafe">
        <p class="h3">
          <span class="badge badge-pill badge-info">Covid-Safe</span>
        </p>
      </div>
      <div class="col-lg-12 mt-3">
        <div class="row">
          <div class="col-lg-4">
            <p><strong>Контакт: </strong></p>
          </div>
          <div class="col-lg-4">
            <p><strong>E-mail: </strong>{{ person.email }}</p>
          </div>
          <div class="col-lg-4"></div>
          <div class="col-lg-4"></div>
          <div class="col-lg-4">
            <p><strong>Телефон: </strong>{{ person.phone }}</p>
          </div>
        </div>
      </div>
      <div class="col-lg-12 mt-3">
        <div class="row">
          <div class="col-lg-4">
            <p><strong>Социјални мрежи: </strong></p>
          </div>
          <div class="col-lg-4">
            <i class="fab fa-facebook fa-2x mr-3"></i>
            <i class="fab fa-twitter fa-2x my-3"></i>
            <i class="fab fa-instagram fa-2x ml-3"></i>
          </div>
          <div class="col-lg-4">
            <router-link
              v-if="person.isAd"
              to="/reservations"
              v-on:click="changeReservations(true, false)"
              class="btn btn-dark rounded-0 ml-5"
              >Резервирај</router-link
            >
            <router-link
              v-if="!person.isAd"
              to="/reservations"
              v-on:click="changeReservations(false, true)"
              class="btn btn-dark rounded-0 ml-5"
              >Откажи</router-link
            >
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- Card End -->
</template>

<script>
export default {
  data() {
    return {
      person: {},
    };
  },
  methods: {
    changeReservations(reserve, cancel) {
      console.log("in details", cancel);
      this.$store.commit("changeReservations", {
        reserve: reserve,
        cancel: cancel,
      });
    },
  },
  mounted() {
    this.person = this.$store.getters.selectedPerson;
  },
};
</script>

