<template>
  <div class="col-lg-12 my-3 border border-dark" style="transform: rotate(0)">
    <router-link
      to="/details"
      v-on:click="addSelectedPerson"
      class="stretched-link"
    ></router-link>
    <div class="row p-3">
      <div class="col-lg-2">
        <img src="../assets/img/profile.png" class="img-fluid" />
      </div>
      <div class="col-lg-8 my-auto">
        <div class="row">
          <div class="col-lg-6 mb-5"><strong>Име: </strong>{{ card.name }}</div>
          <div class="col-lg-6 mb-5">
            <strong>Број на слободни места: </strong>{{ card.freeSeats }}
          </div>
          <div class="col-lg-6 mt-5">
            <strong>Почетна дестинација: </strong>{{ card.startDestination }}
          </div>
          <div class="col-lg-6 mt-5">
            <strong>Крајна дестинација: </strong>{{ card.endDestination }}
          </div>
        </div>
      </div>
      <div class="col-lg-2 my-auto">
        <h1><strong>Цена:</strong></h1>
        <h2>{{ card.price }} MKD</h2>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    card: Object,
    isAd: Boolean,
  },
  methods: {
    addSelectedPerson() {
      let person = this.card;
      person.isAd = this.isAd;
      this.$store.commit("addSelectedPerson", person);
    },
  },
};
</script>

